<script>
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";

/**
 * User grid component
 */
export default {
  page: {
    title: "Client Complaints Grid",
    meta: [
      {
        name: "description",
        content: appConfig.description,
      },
    ],
  },
  components: { Layout, PageHeader },
  data() {
    return {
      title: "Client Complaints Grid",
      items: [
        {
          text: "Complaints Grid",
        },
        {
          text: "Client Complaints Grid",
          active: true,
        },
      ],
      gridList: [
        {
          profile: require("@/assets/images/users/avatar-2.jpg"),
          name: "Jolly Ryles",
          designation: "I booked a parking space but it's status changed to busy before i could park",
        },
        {
          profile: require("@/assets/images/users/avatar-3.jpg"),
          name: "Marion Walker",
          designation: "I booked a parking slot but never parked my car, but i was still charged",
        }
      ],
    };
  },
  middleware: "authentication",
};
</script>

<template>
  <Layout>
    <PageHeader :title="title" :items="items" />

    <div class="row">
      <div
        class="col-xl-3 col-sm-6"
        v-for="(item, index) in gridList"
        :key="index"
      >
        <div class="card text-center">
          <div class="card-body">
            <b-dropdown
              class="float-end"
              variant="white"
              menu-class="dropdown-menu-end"
              right
              toggle-class="font-size-16 text-body p-0"
            >
              <template v-slot:button-content>
                <i class="uil uil-ellipsis-h"></i>
              </template>
              <a class="dropdown-item" href="#">Edit</a>
              <a class="dropdown-item" href="#">Action</a>
              <a class="dropdown-item" href="#">Remove</a>
            </b-dropdown>
            <div class="clearfix"></div>
            <div class="mb-4">
              <img
                v-if="item.profile"
                :src="item.profile"
                alt
                class="avatar-lg rounded-circle img-thumbnail"
              />
              <div class="avatar-lg mx-auto mb-4" v-if="!item.profile">
                <div
                  class="avatar-title bg-soft-primary rounded-circle text-primary"
                >
                  <i
                    class="mdi mdi-account-circle display-4 m-0 text-primary"
                  ></i>
                </div>
              </div>
            </div>
            <h5 class="font-size-16 mb-1">
              <a href="#" class="text-dark">{{ item.name }}</a>
            </h5>
            <p class="text-muted mb-2">{{ item.designation }}</p>
          </div>

          <div class="btn-group" role="group">
            <button type="button" class="btn btn-outline-light text-truncate">
              <i class="uil uil-user me-1"></i> Profile
            </button>
            <button type="button" class="btn btn-outline-light text-truncate">
              <i class="uil uil-envelope-alt me-1"></i> Message
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- end row -->

    <div class="row mt-3">
      <div class="col-xl-12">
        <div class="text-center my-3">
          <a href="javascript:void(0);" class="text-primary">
            <i
              class="mdi mdi-loading mdi-spin font-size-20 align-middle me-2"
            ></i>
            Load more
          </a>
        </div>
      </div>
    </div>
    <!-- end row -->
  </Layout>
</template>
